.productdisplay {
    display: flex;
    margin: 0px 100px;
}

.productdisplay-left {
    display: flex;
    gap: 12px;
}

.productdisplay-img-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.productdisplay-img-list img {
    height: 120px;
}

.productdisplay-main-img {
    width: 450px;
    height: 540px;
}

.productdisplay-right {
    margin: 0px 50px;
    display: flex;
    flex-direction: column;
}

.productdisplay-right h1 {
    color: #3d3d3d;
    font-size: 26px;
    font-weight: 700;
}

.productdisplay-right-stars {
    display: flex;
    align-items: center;
    margin-top: 10px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 14px;
}

.productdisplay-right-prices {
    display: flex;
    margin: 30px 0px;
    gap: 20px;
    font-size: 20px;
    font-weight: 700;
}

.productdisplay-right-price-old {
    color: #818181;
    text-decoration: line-through;
}

.productdisplay-right-price-new {
    color: #ff4141;
}

.productdisplay-right-size h1 {
    margin-top: 40px;
    color: #656565;
    font-size: 18px;
    font-weight: 600;
}

.productdisplay-right-sizes {
    display: flex;
    margin: 20px 0px;
    gap: 15px;
}

.productdisplay-right-sizes div {
    padding: 12px 18px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
}

.productdisplay-right button {
    padding: 15px 30px;
    width: 180px;
    font-size: 14px;
    font-weight: 600;
    color: white;
    background: #ff4141;
    margin-bottom: 30px;
    border: none;
    outline: none;
    cursor: pointer;
}

.productdisplay-right-category{
    margin-top: 10px;
    text-transform: capitalize;
}